.homeContainer{
    width: 100%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    /* border: 2px solid red; */
}

.descriptionContainer{
    display: flex;
    flex-direction: column;
    width: 60%;
    height: 40rem;
    margin-left: 14rem;
    justify-content: center;
}

.heading{
    color: #313131;
    font-size: 2.8rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
}

.heading::after{
    content: '';
    position: absolute;
    width: 5px;
    height: 40px;
    background: #33DCA3;
    margin-left: 2rem;
    margin-top: 0.4rem;
}

.description{
    color: #767676;
    font-size: 1.6rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    margin-top: 1rem;
}

.details{
    width: 60%;
    color: #ADADAD;
    font-size: 1.4rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    margin-top: 1rem;
}

.githubButton{
    width: 14rem;
    height: 4rem;
    background: #DCAA33;
    border: none;
    outline: none;
    cursor: pointer;
    color: #313131;
    font-size: 1.6rem;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 400;
    border-radius: 4px;
    margin-top: 2rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    transition: 0.25s ease-out;
}

.githubButton:hover{
    background: #DCAA33;
    box-shadow: #0000006b 0 2px 8px;
    transform: translateY(-0.2rem);
}